import React, { useEffect } from 'react';

export const Row = ({ item, metadata, onItemChange }) => {
  useEffect(() => {}, []);
  return (
    <tr>
      {Object.keys(metadata).map(key => {
        console.log(metadata[key].type);
        if (key === 'id') {
          return (
            <th key={key} scope="row">
              {item[key]}
            </th>
          );
        } else {
          return (
            <td key={key}>
              {
                <input
                  className="form-control"
                  type={metadata[key].type}
                  value={item[key]}
                  onChange={event => {
                    console.log(item);
                    onItemChange({
                      ...item,
                      [key]: { value: event.target, type: item[key].type },
                    });
                    console.log(item);
                  }}
                />
              }
            </td>
          );
        }
      })}
    </tr>
  );
};
